import React, { FC, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Filters from '../../../components/Sub/Filters/Filters'
import useFilters from '../../../hooks/useFilters'
import useStyles from './AboutProjectPage.style'
import { ICompaniesPageList } from '../../../services/types/companies-page-types/companies-page-types'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import InfoPopup from './InfoPopup/InfoPopup'
import { enqueueSnackbar } from '../../../services/slices/app-slice'
import { ProjectForm, ProjectTabs } from '../../../components/Main/AboutProjectPage'
import { setRole } from '../../../services/slices/auth-slice'
import { resetUpdateStatus, setIsLockUpdate } from '../../../services/slices/workshop-projects-list-page-slice'

const AboutProjectPage: FC<ICompaniesPageList> = ({ setSnack }) => {
  const navigate = useNavigate()
  const classes = useStyles()
  const hook = useFilters()
  const dispatch = useAppDispatch()
  const { filtersJSON, projectToEdit, updateStatus } = useAppSelector((store) => store.workshopProjectsListPage)

  if (!projectToEdit) {
    navigate('/workshop-managers/projects')
  }

  useEffect(() => {
    dispatch(setRole('workshop_manager'))
    dispatch(setIsLockUpdate(true))
  }, [])
  //
  // useEffect(() => {
  //   // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  //   // @ts-ignore
  //   if (deleteNetworkError?.status === 400) {
  //     dispatch(
  //       enqueueSnackbar({
  //         info: {
  //           type: 'error',
  //           title: '  ',
  //           message: '       ',
  //         },
  //         options: {
  //           key: new Date().getTime() + Math.random(),
  //           autoHideDuration: 6000,
  //         },
  //       }),
  //     )
  //     dispatch(clearDeleteError())
  //   }
  // }, [isNetworkError])

  useEffect(() => {
    if (updateStatus === 'ok') {
      setSnack({
        open: true,
        header: '  ',
        message: '      ',
      })
      dispatch(resetUpdateStatus())
    }
  }, [updateStatus])

  return (
    <>
      <main className={classes.root}>
        <Filters
          filterType="aboutProject"
          hook={hook}
          filtersJSON={filtersJSON}
          handleAddVacancy={undefined}
          handleDownload={undefined}
          handleFeedExport={undefined}
          handleFunnelExport={undefined}
          handleAddCohort={undefined}
          handleAddContact={undefined}
          contactsCount={undefined}
          isStatistics={undefined}
        />
        <ProjectForm />
        <ProjectTabs />
        <InfoPopup />
      </main>
    </>
  )
}

export default React.memo(AboutProjectPage)
