import React, { useEffect } from 'react'
import { Box } from '@mui/material'
import qs from 'qs'
import useFilters from '../../../../../hooks/useFilters'
import Filters from '../../../../Sub/Filters/Filters'
import { useAppDispatch, useAppSelector } from '../../../../../services/store/hooks'
import { getProjectStudentsColumns } from './columns'
import DataTable from '../../../../Sub/DataTable/DataTableWithServerPagination'
import {
  getProjectTeam,
  patchStudentStatus,
  patchTeamStatus,
} from '../../../../../services/axios/workshop-projects-list-page'
import { IParams } from '../../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'

const SecondProjectTab = () => {
  const hook = useFilters()
  const dispatch = useAppDispatch()
  const { filtersJSON, projectTeamRowsCount, isLoading, projectTeam } = useAppSelector(
    (store) => store.workshopProjectsListPage,
  )

  const paramsSerializer = (params: IParams) => qs.stringify(params, { arrayFormat: 'repeat' })

  const patchStatus = (studentId: number, status: { status: string }) => {
    dispatch(
      patchTeamStatus({
        id: studentId,
        fieldName: 'statuses',
        fieldValue: filtersJSON.projects_team_statuses.find((item) => item.status === status.status),
      }),
    )
  }
  const columns = getProjectStudentsColumns(patchStatus, filtersJSON.projects_team_statuses, () => null)

  // useEffect(() => {
  //   if (hook.filters.start_date) getStudentsOnPage()
  // }, [hook.filters])

  useEffect(() => {
    // eslint-disable-next-line no-undef
    dispatch(getProjectTeam())
  }, [])

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        gap: '44px',
        marginTop: '12px',
        maxHeight: '500px',
        overflowY: 'scroll',
        scrollbarWidth: 'thin',
        scrollbarColor: '#1a1b2233 #ffffff',
        '&::-webkit-scrollbar': {
          width: 3,
          marginRight: 3,
        },
        '&::-webkit-scrollbar-thumb': {
          backgroundColor: '#1a1b2233',
        },
      }}
    >
      <Filters
        filterType="aboutProjectSecondTab"
        hook={hook}
        filtersJSON={{ ...filtersJSON, statuses: filtersJSON.projects_team_statuses, courses: filtersJSON.courses }}
        handleAddVacancy={undefined}
        handleDownload={undefined}
        handleFeedExport={undefined}
        handleFunnelExport={undefined}
        handleAddCohort={undefined}
        handleAddContact={undefined}
        contactsCount={undefined}
        isStatistics={undefined}
      />
      <DataTable
        sx={{
          '& .MuiDataGrid-cell': { cursor: 'pointer' },
        }}
        // onCellClick={handleCellClick}
        columns={columns}
        rows={projectTeam}
        loading={isLoading}
        rowsCount={projectTeamRowsCount}
        filtersHook={hook}
        sortFieldsMapping={undefined}
      />
    </Box>
  )
}

export default SecondProjectTab
