import React from 'react'
import { Box } from '@mui/material'
import CourseInfo from '../../../../Sub/DataTable/CourseInfo/CourseInfo'
import MultipleCoursesInfo from '../../../../Sub/DataTable/MultipleCoursesInfo/MultipleCoursesInfo'
import StatusSelect from '../../../../Sub/DataTable/StatusSelect/StatusSelect'
import { formatRusStyle } from '../../../../../utils/dates'
import { TGetVacanciesColumns } from '../../../../../services/types/companies-page-types/companies-page-types'
import StudentInfoAvatar from '../../../../Sub/DataTable/StudentInfoAvatar/StudentInfoAvatar'
import ContactsMailIcon from '../../../../../assets/ContactsIcon/ContactsIconComponents/ContactsMailIcon'
import ContactsTgIcon from '../../../../../assets/ContactsIcon/ContactsIconComponents/ContactsTgIcon'
import ContactsWhtsAppIcon from '../../../../../assets/ContactsIcon/ContactsIconComponents/ContactsWhtsAppIcon'
import TableIconWithTooltip from '../../TableIconWithTooltip/TableIconWithTooltip'
import {
  IProjectContact,
  TGetProjectsStudentsColumns,
} from '../../../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'

export const getProjectStudentsColumns: TGetProjectsStudentsColumns = (patchStatus, studentsStatuses, ref) => [
  {
    field: 'id',
    headerName: '#',
    width: 24,
    renderCell: (params) => <CourseInfo course={params.id} />,
    sortable: false,
  },
  {
    field: 'first_name',
    headerName: '',
    width: 265,
    renderCell: (params) => (
      <StudentInfoAvatar
        firstName={params.row?.first_name}
        lastName={params.row?.last_name}
        avatar={params.row?.image}
      />
    ),
    sortable: false,
  },
  {
    field: 'contacts',
    headerName: '',
    width: 115,
    renderCell: (params) => (
      <ul
        style={{
          padding: 0,
          margin: 0,
          listStyle: 'none',
          display: 'flex',
          gap: '5px',
          alignItems: 'center',
        }}
      >
        <Box component="li" sx={{ padding: 0 }}>
          <ContactsMailIcon
            isActive={!!params.row?.contacts.find((contact: IProjectContact) => contact.type === 2)}
            link={params.row?.contacts.find((contact: IProjectContact) => contact.type === 2)?.link}
          />
        </Box>
        <Box component="li" sx={{ padding: 0 }}>
          <ContactsTgIcon
            isActive={!!params.row?.contacts.find((contact: IProjectContact) => contact.type === 1)}
            link={params.row?.contacts.find((contact: IProjectContact) => contact.type === 1)?.link}
          />
        </Box>
        <Box component="li" sx={{ padding: 0 }}>
          <ContactsWhtsAppIcon
            isActive={!!params.row?.contacts.find((contact: IProjectContact) => contact.type === 3)}
            link={params.row?.contacts.find((contact: IProjectContact) => contact.type === 3)?.link}
          />
        </Box>
      </ul>
    ),
    sortable: false,
  },
  {
    field: 'status',
    headerName: '',
    width: 190,
    sortable: false,
    renderCell: (params) => (
      <StatusSelect
        activeStatus={params.value?.name}
        onPatch={patchStatus}
        applicationId={params.row?.id}
        statuses={studentsStatuses}
        width={180}
        refs={null}
      />
    ),
  },
  {
    field: 'pet_project_experience_type',
    headerName: '',
    width: 104,
    renderCell: (params) => <CourseInfo course={params.value ? params.value?.value : ' '} />,
    sortable: false,
  },

  {
    field: 'pet_project_spend_time',
    headerName: '-   ',
    // align: 'left',
    width: 164,
    renderCell: (params) => (
      <CourseInfo
        course={
          params.row?.pet_project_spend_time
            ? `${params.row?.pet_project_spend_time?.min} - ${params.row?.pet_project_spend_time?.max} `
            : ' '
        }
      />
    ),
    sortable: false,
  },
  {
    field: 'created_at',
    headerName: ' ',
    // align: 'left',
    width: 89,
    // renderCell: (params) => <CourseInfo course={params.value} />,
    valueFormatter: (params) => params.value && formatRusStyle(params.value),
    sortable: false,
  },
  {
    field: 'timezone_delta',
    headerName: ' ',
    type: 'string',
    align: 'left',
    width: 88,
    renderCell: (params) => <CourseInfo course={params.value} />,
    sortable: false,
  },
  {
    field: 'comment',
    headerName: '',
    type: 'string',
    align: 'left',
    width: 16,
    renderCell: (params) => <TableIconWithTooltip title={params.value} />,
    sortable: false,
  },
]

export default getProjectStudentsColumns
