import React, { FC } from 'react'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import { useNavigate } from 'react-router-dom'
import useStyles from './InfoPopup.styles'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { closeInfoPopup } from '../../../../services/slices/workshop-projects-list-page-slice'
import { deleteProject } from '../../../../services/axios/workshop-projects-list-page'

const InfoPopup = () => {
  const {
    infoPopupTitle: title,
    infoPopupSubtitle: subtitle,
    infoPopupButtonTitle: buttonTitle,
    isInfoPopupOpen: isOpen,
    infoPopupType: type,
    projectToEdit,
  } = useAppSelector((store) => store.workshopProjectsListPage)
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const navigate = useNavigate()

  const handleClosePopup = () => {
    dispatch(closeInfoPopup())
  }

  const onSubmit = async () => {
    switch (type) {
      case 'delete':
        if (projectToEdit?.id) {
          try {
            await dispatch(deleteProject(projectToEdit?.id))
            dispatch(closeInfoPopup())
            navigate('/workshop-managers/projects')
          } catch {
            return undefined
          }
          return undefined
        }
        return undefined
      // case 'archive':
      //   try {
      //     await dispatch(patchVacancyStatus({ vacancyId: vacancyToArchive, status: { status: '' } }))
      //     dispatch(setVacancyToArchive(null))
      //     dispatch(closeInfoPopup())
      //   } catch {
      //     return undefined
      //   }
      //   return undefined
      default:
        return undefined
    }
  }

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={isOpen} onClose={handleClosePopup}>
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {title}
                </Typography>
                <Typography className={classes.subtitle}>{subtitle}</Typography>
              </div>
            </form>
            <div className={classes.buttonContainer}>
              <Button type="button" className={classes.previousButton} onClick={handleClosePopup}>
                
              </Button>
              <Button className={classes.submitButton} type="button" onClick={onSubmit}>
                {buttonTitle}
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(InfoPopup)
