import React, { FC, useEffect, useMemo, useState } from 'react'
import { Box } from '@mui/material'
import debounce from 'lodash.debounce'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import useStyles from './ProjectForm.style'
import { ControlledSelectField, ControlledTextField } from '../../../Sub/FormElements'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { patchCompanyInfo } from '../../../../services/axios/companies-page'
import { changeCompanyInfoSchema } from '../../../../utils/schemas'
import processServerError from '../../../../utils/server-error'
import { patchProjectInfo } from '../../../../services/axios/workshop-projects-list-page'

const ProjectForm = () => {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const [nameState, setNameState] = useState<string>('')
  const [commentState, setCommentState] = useState<string>('')
  const [customerState, setCustomerState] = useState<string>('')
  const { filtersJSON, projectToEdit } = useAppSelector((store) => store.workshopProjectsListPage)
  const {
    control,
    setValue,
    watch,
    setError,
    reset,
    formState: { errors },
  } = useForm({
    mode: 'onChange',
    resolver: yupResolver(changeCompanyInfoSchema),
    defaultValues:
      {
        ...projectToEdit,
        customer_name: projectToEdit?.customer?.name,
        status: projectToEdit?.status?.id,
        type: filtersJSON?.projects_types?.find((item) => item.value === projectToEdit?.type)?.id,
      } || undefined,
  })

  useEffect(() => {
    reset(
      {
        ...projectToEdit,
        customer_name: projectToEdit?.customer?.name,
        status: projectToEdit?.status?.id,
        type: filtersJSON?.projects_types?.find((item) => item.value === projectToEdit?.type)?.id,
      } || undefined,
    )
  }, [projectToEdit]) // TODO:      ,  

  const status = watch('status')
  const type = watch('type')
  const projectManager = watch('project_manager')
  const scope = watch('scope')
  const name = watch('name')
  const comment = watch('comment')
  const customer = watch('customer_name')

  const changeProjectName = (companyName: string) => {
    if (companyName.length >= 1) setNameState(companyName)
  }

  const debouncedChangeProjectName = useMemo(() => debounce(changeProjectName, 300), [])

  useEffect(() => () => debouncedChangeProjectName.cancel(), [])

  useEffect(() => {
    if (name !== undefined && name !== null) debouncedChangeProjectName(name)
  }, [name])

  const changeProjectComment = (projectComment: string) => {
    if (projectComment.length > 19) setCommentState(projectComment)
  }

  const debouncedChangeProjectComment = useMemo(() => debounce(changeProjectComment, 300), [])

  useEffect(() => () => debouncedChangeProjectComment.cancel(), [])

  useEffect(() => {
    if (comment !== undefined && comment !== null) debouncedChangeProjectComment(comment)
  }, [comment])

  const changeProjectCustomer = (customerName: string) => {
    if (customerName.length >= 1) setCustomerState(customerName)
  }

  const debouncedChangeProjectCustomer = useMemo(() => debounce(changeProjectCustomer, 300), [])

  useEffect(() => () => debouncedChangeProjectCustomer.cancel(), [])

  useEffect(() => {
    if (customer !== undefined && customer !== null) debouncedChangeProjectCustomer(customer)
  }, [customer])

  useEffect(() => {
    if (projectToEdit && nameState !== projectToEdit.name && nameState?.length > 1)
      dispatch(patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'name', fieldValue: nameState }))
  }, [nameState])

  useEffect(() => {
    if (projectToEdit && commentState !== projectToEdit.comment && commentState?.length > 1)
      dispatch(patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'comment', fieldValue: commentState }))
  }, [commentState])

  useEffect(() => {
    if (projectToEdit && customerState !== projectToEdit.customer.name && customerState?.length > 1)
      // TODO  ,            
      dispatch(
        patchProjectInfo({
          id: projectToEdit?.id || 0,
          fieldName: 'customer',
          fieldValue: { ...projectToEdit?.customer, name: customerState },
        }),
      )
  }, [customerState])

  const handleStatusChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue('status', +e.target.value, { shouldValidate: true, shouldDirty: true })
    // TODO      ,         ,        
    const newStatus = filtersJSON.projects_statuses.find((item) => item.id === +e.target.value)
    dispatch(patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'status', fieldValue: { ...newStatus } }))
  }

  const handleTypeChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue('type', +e.target.value, {
      shouldValidate: true,
      shouldDirty: true,
    })
    const newType = filtersJSON.projects_types.find((item) => item.id === +e.target.value)
    dispatch(patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'type', fieldValue: newType?.value }))
  }

  const handleProjectManagerChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue('project_manager', +e.target.value, {
      shouldValidate: true,
      shouldDirty: true,
    })
    dispatch(
      patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'project_manager', fieldValue: +e.target.value }),
    )
  }

  const handleScopeChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue('scope', +e.target.value, {
      shouldValidate: true,
      shouldDirty: true,
    })
    dispatch(patchProjectInfo({ id: projectToEdit?.id || 0, fieldName: 'scope', fieldValue: +e.target.value }))
  }

  return (
    <form className={classes.form}>
      <Box className={classes.smallFieldsContainer}>
        <ControlledTextField
          control={control}
          fieldName="name"
          labelTitle=" "
          placeholder="  "
          error={!!errors.name?.message}
          helperText={errors.name?.message}
        />
        <ControlledSelectField
          labelTitle=" "
          value={type}
          onChange={handleTypeChange}
          items={filtersJSON?.projects_types?.map((item) => ({ ...item, name: item.value })) || []}
          tipContent="  "
          error={!!errors?.type?.message}
          helperText={errors?.type?.message}
          maxWidth={420}
        />
        <ControlledSelectField
          labelTitle=" "
          value={scope}
          onChange={handleScopeChange}
          items={filtersJSON?.scopes?.map((item) => ({ ...item, name: item.value })) || []}
          tipContent="  "
          error={!!errors?.scope?.message}
          helperText={errors?.scope?.message}
          maxWidth={420}
        />
        <ControlledSelectField
          labelTitle=" "
          value={projectManager}
          onChange={handleProjectManagerChange}
          items={filtersJSON?.profiles || []}
          tipContent=" "
          error={!!errors?.project_manager?.message}
          helperText={errors?.project_manager?.message}
          maxWidth={420}
        />
      </Box>
      <Box className={classes.bigFieldsContainer}>
        <Box className={classes.fieldsRow}>
          <ControlledTextField
            control={control}
            fieldName="customer_name"
            labelTitle=" "
            placeholder="  "
            error={!!errors.name?.message}
            helperText={errors.name?.message}
          />
          <ControlledSelectField
            labelTitle=" "
            value={status}
            onChange={handleStatusChange}
            items={
              filtersJSON?.projects_statuses?.map((item) => ({
                ...item,
                name: item.status,
              })) || []
            }
            tipContent="  "
            error={!!errors?.status?.message}
            helperText={errors?.status?.message}
            maxWidth={280}
          />
        </Box>
        <ControlledTextField
          labelTitle=""
          placeholder="         "
          fieldName="comment"
          control={control}
          error={!!errors.comment?.message}
          helperText={errors.comment?.message}
          multiline
          rows={10}
          symbolsCount={comment?.length || 0}
          minLength={20}
          maxLength={500}
        />
      </Box>
    </form>
  )
}

export default ProjectForm
